.profile{
  border-radius: 50%;
  border: 5px solid #FCE540;
  width: 20%;
  height: 20%;
}
span{
    font-family:'cheonri';
    font-weight:bold;
    font-size:xx-large;
}
.input-file-button{
    padding: 6px 25px;
    background-color:#0D6EFD;
    border-radius: 4px;
    color: white;
    cursor: pointer;
  }

  .groups_border{
    padding: 0px;
    margin: 0px;
  }
  .groups_border > .jacketInfo{
    display: inline-block;
      /* You can also add some margins here to make it look prettier */
    zoom:1;
   
    width: 100px;
    height: 100px;
    margin: 10px 10px;
    /* this fix is needed for IE7- */
  }
  
  .chartToolTip{
    width:"100px";
    white-space: pre-line;
  }

  .groups{
    width: 300px;
    height: 125px;
  }
  
  .other{
    width: 100%;
    max-width:125px;
    display:block;
    margin: 0 auto;
    height: 100%;
    max-height: 125px;
  }
  
  .jackets{
    width: 100%;
    max-width:100px;
    display:block;
    margin: 0 auto;
    height: 100%;
    max-height: 100px;
  }

  #profile_img{
    width:150px; 
    height:150px;
  }

  @media only screen and (max-width: 600px) {
    /* For Mobile */
    .input-file-button{
      padding: 6px 10px;
      background-color:#0D6EFD;
      border-radius: 4px;
      color: white;
      cursor: pointer;
    }
  
    .groups_border{
      padding: 0px;
    }
    .groups_border > .jacketInfo{
      display: inline-block;
        /* You can also add some margins here to make it look prettier */
      zoom:1;
      *display:inline;
      width: 75px;
      height: 75px;
      margin: 10px 5px;
      
      /* this fix is needed for IE7- */
    }
    
    .chartToolTip{
      width:"100px";
      white-space: pre-line;
    }
  
    .groups{
      width: 250px;
      height: 100px;
    }
    
    .other{
      width: 100%;
      max-width:125px;
      display:block;
      margin: 0 auto;
      height: 100%;
      max-height: 125px;
    }
    
    .jackets{
      width: 100%;
      max-width:75px;
      display:block;
      margin: 0 auto;
      height: 100%;
      max-height: 75px;
    }

    #profile_img{
      width:150px; 
      height:150px;
    }
    
    
  }
